ion-menu {
	button{
		height: 40px;
	}
	.menu-content
	{
		background-color: color($colors, background, base);
	}
	.menu-list
	{
		//margin-top: 20vh;
		.item
		{
			background-color: color($colors, background, base);
			padding-right: 25px;
			&:first-child
			{
				border-top: solid 1px rgba($white-d, 0.15);
			}

			&:last-child
			{
				border-bottom: solid 1px rgba($white-d, 0.15);
			}

			.item-inner
			{
				border-bottom: 1px solid rgba($white-d, 0.15);
				font-size: 1.6rem;
				font-weight: 300;
				letter-spacing: 1px;
				color: $theme-color-3;
			}

			.item-icon
			{
				color: $theme-color-2;
				font-size: 3.4rem;
			}

			&.activated
			{
  			background-color: $theme-color-1;
  			transition-duration: 0ms;
				.item-inner
				{
					color: $white;
				}
				.item-icon
				{
					color: $white;
				}
			}
		}
	}
}
